<template>
  <div id="login">
    <div>
      user : <input type="text" v-model="user">
    </div>
    <div>
      pwd : <input type="password" v-model="pwd">
    </div>
    <input type="button" value="login" @click="login">
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      user: '',
      pwd: ''
    }
  },
  methods: {
    login() {
      this.$router.push({ name: 'Home', params: { user: this.user, pwd: this.pwd } });
    }
  }
}
</script>

<style>
#login > * {
  margin: 30px 0;
}
</style>
